.button {
  padding: 1rem 2.2rem;
  cursor: pointer;
  border-radius: 5px;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 17px;
  transition: all .3s ease-in-out;

}

.button:disabled {
  background-color: #ccc;
  color: black;
  cursor: initial
}
@media screen and (max-width: 420px){
  .button{
    font-size: 12px;
    padding: 0.7rem 1.4rem;
  }
}