.aside {
  flex-basis: 40%;
  background: #ffffff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 30px;
  margin-left: 50px;
}

.span1 {
  font-family: var(--gilroy-bold);
  font-size: 20px;
  color: #323232;
}
.span2 {
  font-size: 14px;
  color: #323232;
}

.mobile_link {
  display: none;
  background-color: #fff;
  text-align: center;
  width: 100%;
  padding: 0rem 2rem;
}
.mobile_button > button {
  background-color: #eab221;
  color: #fff;
  border: none;
  padding: 0.8rem 1.5rem;
}
.mobile_link > div {
  width: 100%;
  margin: 1rem auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 1035px) {
  .aside {
    display: none;
  }
  .mobile_link {
    display: block;
    position: fixed;
    bottom: 0px;
    left: 0px;
    z-index: 1;
  }
  .mobile_link p {
    font-size: 14px;
  }
}
