div[bookingSession="Not Checked In"] {
  background: #ffedec;
  color: #eb6c63;
}
div[bookingSession="Checked In"] {
  background: #e4f7f3;
  color: #08a47d;
}

.book {
  border: 1px solid #eff2f7;
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: 0.5fr 0.5fr 1fr 0.8fr 0.8fr 0.4fr;
  grid-column-gap: 20px;
  justify-content: center;
  align-items: center;
  position: relative;
}
.book_img img {
  width: 100%;
  border-radius: 5px;
}
.book_details {
  text-align: left;
}
.locate {
  display: flex;
  gap: 4px;
  margin: auto;
}

.book_date {
  background-color: #12082d;
  padding: 0.6rem;
  border-radius: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9fafc;
  gap: 5px;
}

.book_type {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}

.dots {
  display: flex;
  background: #8080801a;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  padding: 1px;
}
.dots span {
  width: 4px;
  height: 4px;
  background: #8080805c;
  margin: 0px 1px;
  border-radius: 50px;
}

.book_action {
  cursor: pointer;
  position: relative;
}

.popup {
  background: #eab221;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  z-index: 1;
  padding: 0.7rem 1.5rem;
  width: 12rem;
  color: #fff;
  top: 36px;
  left: -125px;
}
.popup ul {
  list-style: none;
  font-family: var(--gilroy-regular);
}
.popup ul li {
  margin: 20px 0;
  cursor: pointer;
}
@media screen and (max-width: 940px) {
  .book {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto auto auto auto;
    justify-content: center;
    align-items: center;
  }

  .book_img,
  .book_type,
  .book_date {
    grid-column-start: 1;
    grid-column-end: 5;
  }
  .book_details {
    grid-column-start: 1;
    grid-column-end: 4;
  }
  .book_action {
    grid-column-start: 1;
    grid-column-end: 5;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .locate {
    margin-left: auto;
  }
  .book_action {
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}
.ldsellipsis {
  display: inline-block;
  position: relative;
  width: 70px;
  height: 10px;
  margin: auto;
}
.ldsellipsis div {
  position: absolute;
  top: 0px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.ldsellipsis div:nth-child(1) {
  left: 8px;
  animation: ldsellipsis1 0.6s infinite;
}
.ldsellipsis div:nth-child(2) {
  left: 8px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(3) {
  left: 32px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(4) {
  left: 56px;
  animation: ldsellipsis3 0.6s infinite;
}
@keyframes ldsellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes ldsellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes ldsellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
