.mainContainer {
  padding: 2rem 4rem;
  margin: 5rem auto;
  width: 85vw;
  background: #ffffff;
  display: flex;
  justify-content: space-between;
  border-radius: 20px;
}
.mainContainer span {
  background: #eaedee;
  height: inherit;
  width: 3px;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.container > div {
  display: flex;
  justify-content: space-around;
}
.container > div > div {
  margin-right: 2rem;
  width: 30px;
  height: 30px;
  border-radius: 50px;
}
.div2 {
  color: #9da8b6;
}
.container > div > p {
  color: #9da8b6;
}
.container > p {
  font-size: 2.1rem;
  color: #12082d;
  font-weight: bold;
}
.div1 {
  background: #ef8f6e;
}
.div2 {
  background: #63bd88;
}
.div3 {
  background: #2f66a7;
}
.div4 {
  background: #b64967;
}

@media screen and (max-width: 900px) {
  .mainContainer {
    flex-direction: column;
  }
  .mainContainer span {
    width: 100%;
    height: 2px;
    margin: 5px 0;
  }
}
@media screen and (max-width: 660px) {
  .mainContainer {
    flex-direction: column;
    align-items: flex-start;
  }
  .container {
    justify-content: flex-start;
  }
  .container > p {
    margin-bottom: 20px;
  }
  .container > div > p {
    margin-top: 20px;
  }
  .container > div > div {
    margin-top: 24px;
  }
  .container > p:last-of-type {
    text-align: center;
  }
}
