.container {
  width: 85%;
  margin: 4rem auto auto auto;
}
.first {
  font-size: 30px;
  color: #12082d;
  margin-bottom: 3rem;
  margin-top: 2rem;
}
.content {
  justify-content: center;
  align-items: center;
  text-align: left;
}
.content p {
  color: #222222;
}
.content p:first-of-type {
  font-size: 24px;
}
.content p:last-of-type {
  font-size: 16px;
}
.btn {
  width: 300px;
  background: #eab221;
  border-radius: 5px;
  color: #ffffff;
  padding: 10px;
  border: none;
  gap: 10px;
  cursor: pointer;
}
.set {
  font-size: 25px;
  color: #222222;
  align-items: center;
  text-align: center;
}
.form input {
  padding: 10px;
}
.form input,
.span {
  width: 100%;
  border: 1px solid #ccc6c6;
  border-radius: 8px;
  outline: none;
}
.span {
  padding: 10px;
  height: 40px;
  display: block;
  background: rgba(211, 211, 211, 0.8);
  border: 2px solid #ccc6c6;
  position: relative;
}
.label {
  font-size: 16px;
  margin-top: 20px;
  margin-bottom: 10px;
  display: block;
  color: #222222;
}
.save {
  background: #eab221;
  border-radius: 5px;
  border: none;
  width: 100%;
  color: #ffffff;
  padding: 10px;
  position: relative;
  cursor: pointer;
}
.modal_container {
  margin-top: 3rem;
  padding: 2rem;
  background: #fff;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 50%;
  /* border: 1px solid grey; */
}
.modal_container > div {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.border {
  background: rgba(99, 99, 102, 0.2);
  width: 100%;
  height: 2px;
  display: block;
  margin: 15px 0;
}
.modal_container p {
  color: #eab221;
  text-align: left;
  font-weight: bold;
  width: 150px;
}
.modal_container span {
  color: #7b7b7b;
  font-weight: bold;
}
.modal_container button {
  background: transparent;
  border: 1px solid rgba(18, 8, 45, 0.4);
  border-radius: 10px;
  padding: 10px;
  color: #ff5757;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.modal_container button img {
  margin: 0px 5px;
}
