.hero {
  position: relative;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 9rem;
  background-image: linear-gradient(to bottom, #00000054, #00000054),
    url("../../../assets/images/landing/bgdesktop.webp");
}

.hero_container {
  display: flex;
  height: 100%;
  flex-direction: column;
  min-height: 100vh;
}

.availability {
  flex-grow: 1;
  z-index: 1;
}

/* Explore CSS */

.explore {
  background: linear-gradient(
    1.48deg,
    #0d1c2e80 -34.84%,
    rgba(13, 28, 46, 0.15) 69.1%
  );
  mix-blend-mode: normal;
  display: flex;
  margin-top: 3rem;
  position: relative;
}

.explore_container {
  width: 100vw;
  padding: 8rem 0 0 5rem;
}

.explore_content {
  width: 300px;
}

.explore_btns {
  margin-top: 50px;
  padding-bottom: 3rem;
  display: flex;
  padding-bottom: 2rem;
}

.explore_btns > img {
  margin-right: 15px;
}

/* Location CSS */
.locations {
  position: absolute;
  bottom: -10rem;
  /* left: 0; */
  display: flex;
  /* justify-content: flex-start; */
  right: 0;
  width: 75vw;
  overflow-x: scroll;
  align-items: center;
  overflow-y: hidden;
}

.locations_flex {
  display: flex;
  width: 95vw;
}

.locations::-webkit-scrollbar {
  height: 0;
  width: 0;
}
.location {
  margin-right: 1rem;
  width: 364px;
  height: 550px;
}

.place {
  font-family: var(--gilroy-semibold);
  font-size: 24px;
  letter-spacing: 0.01em;
  color: #ffffff;
  padding-left: 30px;
  position: absolute;
  top: -7rem;
  transition: 0.4s linear;
}

.description {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: rgba(255, 255, 255, 0.6);
  padding-left: 30px;
  position: absolute;
  top: -4rem;
}

.a:hover .place {
  background-color: #eab221;
  /* brightness(0.65); */
  width: 100%;
  padding-bottom: 78px;
  padding-top: 32px;
  border-radius: 0 0 10px 10px;
  margin-top: -31px;
}

.a:hover .description {
  color: #fff;
}

.a:hover .img {
  filter: brightness(1.3);
}


.description {
  font-family: var(--gilroy-regular);
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: rgba(255, 255, 255, 0.6);
  padding-left: 30px;
  position: absolute;
  top: -4rem;
}

.img {
  border-radius: 10px;
  filter: brightness(1.2);
  transition: 0.4s linear;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.div {
  position: relative;
}

@media screen and (max-width: 1520px) {
  .explore_container {
    padding: 8rem 0 0 2.5rem;
  }
  .locations > div {
    transform: scale(0.8, 0.8);
  }
}
@media screen and (max-width: 1110px) {
  .explore_container {
    padding: 3rem 0 0 1.8rem;
    margin-bottom: 60px;
  }
  .explore_content {
    width: 65%;
    position: relative;
    margin-right: auto;
  }

  .explore_btns {
    position: absolute;
    right: -55%;
    bottom: -2rem;
    transform: scale(0.75, 0.75);
  }
  .locations {
    bottom: -28.5rem;
    /* left: -2rem; */
    width: 100vw;
    /* height: 28rem; */
  }
  .locations > div {
    transform: scale(0.8, 0.8);
  }
}

@media screen and (max-width: 550px) {
  .locations {
    /* width: 113vw; */
    /* left: -75px; */
    bottom: -15.5rem;
    /* height: 19.5rem; */
  }
  .location {
    width: 204px;
    height: 310px;
    font-size: 1rem;
  }
  .place {
    font-size: 1.3rem;
  }
}
