div[booking_session="Not Checked In"] {
  background: #ffedec;
  color: #eb6c63;
}
div[booking_session="Checked In"] {
  background: #e4f7f3;
  color: #08a47d;
}

.book {
  border: 1px solid #eff2f7;
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: 0.5fr 0.5fr 1fr 0.8fr 0.8fr 0.4fr;
  grid-column-gap: 20px;
  justify-content: center;
  align-items: center;
}
.book_img img {
  width: 100%;
  border-radius: 5px;
}
.book_details {
  text-align: left;
}
.book_action {
  position: relative;
}
.locate {
  display: flex;
  gap: 4px;
  margin: auto;
}

.book_date {
  background-color: #12082d;
  padding: 0.6rem;
  border-radius: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f9fafc;
  gap: 5px;
}

.book_type {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}

.hover_div {
  position: absolute;
  top: 39px;
  right: 55px;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.5s, opacity 0.7s ease-in-out;
}
.arrow_up {
  width: 20px;
  height: 20px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #eab221;
  position: absolute;
  top: -20px;
  right: 30px;
}
.hover {
  border-radius: 5px;
  padding: 15px 20px;
  background-color: #eab221;
  width: 180px;
}
.hover_div span {
  display: block;
  height: 2px;
  width: 100%;
  padding: 10px 0;
}
.hover_div a {
  color: #fff;
}
.hover_div p {
  color: grey;
  cursor: not-allowed;
}
.book_action:hover .hover_div,
.hover_div:hover {
  visibility: visible;
  opacity: 1;
}
@media screen and (max-width: 940px) {
  .book {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto auto auto auto;
    justify-content: center;
    align-items: center;
  }

  .book_img,
  .book_type,
  .book_date {
    grid-column-start: 1;
    grid-column-end: 5;
  }
  .book_details {
    grid-column-start: 1;
    grid-column-end: 4;
    /* text-align: left; */
  }
  .locate {
    margin-left: auto;
  }
  .book_action {
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}
