.form {
  /* width: 60%; */
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  padding: 1.5rem;
}
.form >div{
  margin: 0px 10px
}
.form p {
  padding: 10px 0;
  font-size: 16px;
  color: #12082d;
}

.form input {
  font-family: var(--gilroy-medium);
  font-size: 16px;
  color: #12082d;
  border: none;
  outline: none;
}
.form select {
  font-family: var(--gilroy-medium);
  font-size: 16px;
  color: #B8BABA;
  /* color: #12082d; */
  border: none;
  outline: none;
}
.suggestion_div {
  display: flex;
  justify-content: space-between;
}
.datepicker_div,
.input_div {
  position: relative;
}
.suggestions {
  width: 9.5rem;
}
.suggestions input{
  min-height: 46px;
}
.filter_items{
  width: 162px;
}
.line{
  border: 1px solid #B9B9BA;
  height: 65px;
  width: 0px;
}
/* .filter_item{
  border: 1px solid #B9B9BA;
} */
.filter_items select{
  width: 150px;
  min-height: 46px;
  outline: none;
  border: none;
  appearance: none;
  cursor: pointer;
}
.filter_items input{
  outline: none;
  width: 143px;
  border-radius: 5px;
  height: 30px;
  border: 0.3px solid #B8BABA;
}
.price_select{
  position: relative;
  cursor: pointer;
  color: #B8BABA !important;
}
.price{
  border: 0.3px solid #fff;
  padding: 18px 9px;
  border-radius: 7px;
  position: absolute;
  /* top: 0px; */
  background-color: #fff;
  display: block;
}
.hide_price{
  display: none;
}

.options{
  position: absolute;
  /* top: 70px; */
  width: 100%;
  background: white;
  padding: 3px 10px 0;
  max-height: 40vh;
  overflow-y: scroll;
  z-index: 100;
}
.form input:focus {
  border-bottom: 2px solid #12082d;
}

.form .datespan button {
  box-shadow: none;
}
.datespan {
  display: flex;
}
.datespan img {
  margin-left: 10px;
}

@media screen and (max-width: 1160px) {
  .form {
    width: 50%;
    flex-direction: column;
  }
  .form .button_div button {
    padding: 1rem 0.8rem;
  }
  .datespan {
    justify-content: space-between;
  }
  .suggestions {
    width: 100%;
  }
  .line{
    display: none;
  }
  /* .input_div div {
    display: flex;
    justify-content: space-between;
  } */
  .datepicker_div,
  .input_div {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #f1f6f8;
    width: 95%;
  }
  .filter_items{
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #f1f6f8;
    width: 95%; 
  }
  .filter_item{
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .form {
    width: 80%;
  }
}
@media screen and (max-width: 320px) {
  .form {
    width: 100%;
  }
}
