.starDiv {
  display: flex;
  background: rgba(234, 178, 33, 0.05);
  border-radius: 10px;
  padding: 20px;
  align-items: center;
}

.starDiv span {
  width: 50px;
  height: 50px;
  background: #eab221;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
}

.starDiv div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.feature {
  border: 1px solid #eff0f2;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  font-size: 18px;
  font-family: var(--gilroy-regular);
  color: #12082d;
  margin: 40px 0;
}

.feature div {
  padding: 1rem 0;
}
.feature span {
  background: #eff0f2;
  width: 2px;
  height: inherit;
}

.feature div img {
  margin-right: 12px;
}
@media screen and (max-width: 780px) {
  .feature {
    font-size: 15px;
  }
  .feature div {
    padding: 0.6rem 0;
  }
}
@media screen and (max-width: 630px) {
  .starDiv span {
    height: 35px;
  }
}
