.pop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(34, 34, 34, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner {
  position: relative;
  width: 100%;
  background: #ffffff;
  padding: 30px;
  max-width: 640px;
  border-radius: 15px;
}

.innerspan {
  position: absolute;
  top: 33px;
  right: 16px;
  cursor: pointer;
  font-size: 20px;
}

@media screen and (max-width: 900px) {
  .inner {
    max-width: 400px;
  }
}
@media screen and (max-width: 400px) {
  .inner {
    max-width: 300px;
  }
}
