div [data-testid="DateRangeInputGrid"] {
  display: flex;
}
div [data-testid="DateRangeInputGrid"] + div {
  top: 50px;
  right: -30px;
  left: unset;
}
div [data-testid="DateRangeInputGrid"] label input {
  padding: 0px;
}
div [data-testid="DateRangeInputGrid"] > div {
  display: none;
}
.form {
  width: 100%;
}

.form_group {
  margin-bottom: 2rem;
}

.form_input {
  margin-top: 1rem;
  position: relative;
}

.form_input-svg {
  display: flex;
  align-items: center;
}

.input {
  width: 100%;
  position: relative;
}

.input img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.input img:first-of-type {
  left: 30px;
  top: 23px;
}

.input img:last-of-type {
  right: 30px;
  top: 28px;
}

.form_input input {
  border: 1px solid #eff2f7;
  border-radius: 10px;
  width: 100%;
  padding: 1rem 4rem;
  font-size: 1rem;
}

.form_input input:focus {
  border-radius: 10px;
  border: 1px solid #eab221;
  outline: none;
}

.form_input input[type="password"]::placeholder {
  font-size: 1.5rem !important;
}

.form button {
  width: 100%;
}
.invalid {
  border: 1px solid red !important;
}
@media screen and (max-width: 1035px) {
  div [data-testid="DateRangeInputGrid"] + div {
    top: 50px;
    right: unset;
    left: 0px;
  }

}
@media screen and (max-width: 950px) {
  div [data-testid="MonthGrid"] {
    display: flex;
    flex-direction: column;
  }
  div [data-testid="MonthGrid"] > div {
    margin-bottom: 15px;
  }
  div [data-testid="DateRangeInputGrid"] + div > div {
    margin-top: 30px;
    padding: 10px;
    transform: scale(1.1);
  }
}
@media screen and (max-width: 700px) {
  .form_input input {
    font-size: 0.9rem;
    padding: 1rem 2.9rem 1rem 4rem;
  }
}

label[for="startDate"] {
  border: none !important;
}

label[for="endDate"] {
  border: none !important;
}
