.main {
  margin-top: 9rem;
  width: 100%;
}
.main div:first-of-type {
  background-image: linear-gradient(rgba(0, 0, 0, 0.527), rgba(0, 0, 0, 0.527)),
    url("../../../assets/images/aboutUs/bg.png");
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  height: 15.7rem;
  background-color: #000000;
}
.main h2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.main div:last-of-type {
  width: 85%;
  margin: auto;
}
.main h3 {
  text-align: center;
  font-weight: bold;
  color: rgba(34, 34, 34, 0.9);
  margin: 3rem 0;
}
.main p,
.main ul li {
  color: #222222;
}
.main ul li {
  margin: 1.5rem 0;
  font-family: var(--gilroy-regular);
  line-height: 40px;
}
.main p {
  line-height: 44px;
  margin: 3rem 0;
}
.main span {
  width: 90%;
  margin: auto;
  display: block;
  background: #c4c4c4;
  height: 2px;
}
