.shrink > div {
  width: auto !important;
  height: 100vh !important;
  z-index: 100 !important;
  overflow: hidden;
}

.aprtment_details {
  background: rgba(255, 255, 255, 0.88);
}
.apartment_container {
  width: 85%;
  margin: auto;
}
.apartment_details h1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-family: var(--gilroy-medium);
}
.apartment_details h1 > span {
  color: #eab221;
  text-decoration: underline;
  font-size: 1.4rem;
  margin-left: 55px;
  cursor: pointer;
}
.info {
  display: flex;
  justify-content: space-between;
}

.info img {
  padding-right: 8px;
}

.info span {
  padding-right: 15px;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 4rem;
  margin-top: 3rem;
}

.map {
  padding: 80px 0px;
}

.map img {
  width: 100%;
}
@media screen and (max-width: 972px) {
  .flex {
    flex-direction: column-reverse;
  }
  .flex > aside {
    width: 100%;
    margin-left: 0;
    margin-bottom: 20px;
  }
}
