.tabs {
    margin: 3rem 0;
  display: flex;
  justify-content: space-between;
}
.tabDiv1,
.tabDiv1 form,
.tabDiv2,
.tabDiv2 div,
.tabDiv1 button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabDiv1 span {
  margin-right: 10px;
  background: #f9fafc;
  border-radius: 5px;
  font-size: 14px;
  border: 2px solid #eab221;
  transition: 0.3s ease;
  padding: 10px 20px;
}
.tabDiv1 span:hover {
  background: #eab221;
  color: #ffffff;
}
.tabDiv1 input {
  padding: 10px 20px;
  font-size: 12px;
  color: #636366;
  border: 1px solid rgba(99, 99, 102, 0.1);
  font-family: var(--gilroy-regular);
  font-size: 14px;
  border-radius: 8px 0 0 8px;
}
.tabDiv1 input:focus {
  outline: 1px solid #eab221;
}
.tabDiv1 button img {
  padding: 13.5px 20px;
}
.tabDiv1 button {
  border: none;
  background: #eab221;
  border-radius: 0 8px 8px 0;
}
.tabDiv2 img {
  padding: 5px 10px;
  background: rgba(196, 196, 196, 0.58);
  border-radius: 5px;
}

.tabDiv2 div, .tabDiv2 span{
    margin-right: 10px;
}

@media screen and (max-width: 1000px){
  .atb{
    display: none;
  }
}