.content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  margin-top: 2rem;
}

.label {
  background: rgba(234, 178, 33, 0.1);
  border-radius: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
  cursor: pointer;
}

.label span {
  width: 24px;
  height: 24px;
  border: 5px solid #ffffff;
  border-radius: 50%;
  position: relative;
}

.label span::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 14px;
  border-radius: 50%;
  transition: all 0.4s ease-in-out;
}
.grey {
  background-color: #e4e4e4;
}
.grey span {
  display: none;
}
.input:checked ~ .label span::after {
  background: #eab221;
}

.label P {
  font-weight: 600;
}

.term {
  color: var(--gold);
  text-decoration: underline;
  font-weight: 700;
  margin: 0 7px;
}

.apart button {
  width: 100%;
}
.check_label {
  font-size: 17px;
  cursor: pointer;
  position: relative;
  padding-left: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.check_label input {
  margin-right: 12px;
  width: 0px;
  height: 0px;
  opacity: 0;
}
.check_label span {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  border: 0.5px solid rgba(99, 99, 102, 0.6);
  border-radius: 5px;
}
.check_label .check_span:after {
  content: "";
  position: absolute;
  display: none;
}
.check_label .check_span:after {
  left: 6px;
  top: 1px;
  width: 4px;
  height: 9px;
  border: solid #eab221;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.check_label input:checked ~ .check_span:after {
  display: block;
}
.check_label input:checked ~ .check_label {
  background-color: #eab221;
}
