.shrink > div{
  width: auto !important; 
  height: 100vh !important;
  z-index: 100 !important;
  overflow: hidden;
} 
.info {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.info img {
  padding-right: 8px;
}

.info > span > span {
  padding-right: 10px;
}
.share {
  cursor: pointer;
  padding: 10px;
}
.images {
  display: flex;
  border-radius: 10px;
  position: relative;
  gap: 14px;
  margin: 40px 0px;
}

.images span {
  position: absolute;
  top: 10px;
  left: 8px;
  background: rgba(18, 8, 45, 0.8);
  border-radius: 4px;
  font-size: 12px;
  color: #ffffff;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  gap: 7px;
}

.div1 {
  flex-basis: 60%;
}

.div1 img {
  width: 100%;
}

.div2 {
  flex-grow: 1;
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 20px;
}

.div2 img {
  width: 100%;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 4rem;
  position: relative;
}
.map {
  padding: 80px 0px;
}
.map div{
  z-index: 0;
}
.map img {
  width: 100%;
}
@media (max-width: 1035px) {
  .div1 {
    flex-basis: 100%;
  }
  .div2 {
    display: none;
  }
  .flex {
    flex-direction: column;
  }
}
@media screen and (max-width: 680px) {
  .info {
    font-size: 13px;
  }
  .info img {
    transform: scale(0.9, 0.9);
  }
  .map > div > div {
    width: 100%;
  }
  .map > div > div > h4,
  .map > div > div > p {
    padding-left: 2rem;
  }
  .map img {
    height: 35vh;
  }
}
