.how_section {
  text-align: center;
  margin-top: 8rem;
  margin-bottom: 8rem;
}
.how_section > div {
  display: flex;
}
.p_text{
    /* margin: 20px 0 50px 0; */
    font-size: 16px;
    text-align: center;
    margin-bottom: 50px;
    color: #636366;
}
.how_box {
  display: flex;
  margin: 80px 20px;
}
.how_box p {
  text-align: left;
}
.how_box div:last-child {
  width: 60%;
  margin-left: 20px;
}
.how_box > div > p:first-of-type {
  font-size: 24px;
  color: #12082d;
  font-weight: bolder;
}
.how_box > div > P:last-of-type {
  font-size: 18px;
  line-height: 200%;
  color: #636366;
}

@media screen and (max-width: 900px){
  .firstimg {
    display: none;
  }
  .how_box div:last-child {
  width: 100%;
}
  .how_box{
    display: flex;
    flex-direction: column;
  }
  .p_text{
    margin-bottom: -35px;
  }
  .text{
    text-align: center;
  }
  .text p:first-of-type{
    text-align: center;
    font-family: Recoleta;
font-style: normal;
font-weight: bold;
font-size: 16px;
margin-right: 48px;
/* line-height: 144%; */
