.nav {
  position: relative;
  left: 20px;
  margin-right: auto;
}

.nav_ul {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
}

.nav_li {
  margin-right: 3rem;
}

.nav_a {
  font-family: var(--gilroy-regular);
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
  text-decoration: none;
  color: #ffffff;
}

.nav_fixed .nav_ul .nav_li .nav_a {
  color: #12082d !important;
}
