.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mt-sm {
  margin-top: 1.5rem;
}

.mt-md {
  margin-top: 2.5rem;
}

.mt-bg {
  margin-top: 4rem;
}

.mb-sm {
  margin-bottom: 1.5rem;
}

.mb-md {
  margin-bottom: 2.5rem;
}

.mb-bg {
  margin-bottom: 4rem;
}

.mr-sm {
  margin-right: 1rem;
}

.ta {
  text-align: center;
}

.mt-130 {
  margin-top: 130px;
}
.error {
  margin-top: 1rem;
  text-align: center;
  color: red;
}
.border {
  border: 1px solid rgba(99, 99, 102, 0.1);
  border-radius: 8px;
  font-size: 14px;
  color: #636366;
  padding: 1rem 1.5rem;
  cursor: pointer;
}

.border:hover {
  background-color: #ecb321;
  color: white;
}

.container {
  width: 85%;
  margin: auto;
}

@media (min-width: 2000px) {
  .container {
    width: 70%;
  }
}

@media (min-width: 2900px) {
  .container {
    width: 60%;
  }
}
@media screen and (max-width: 1035px) {
  .image-gallery-icon {
    z-index: 0;
  }
}
@media screen and (max-width: 768px) {
  .mt-sm {
    margin-top: 1rem;
  }

  .mt-md {
    margin-top: 1.8rem;
  }

  .mt-bg {
    margin-top: 2rem;
  }

  .mb-sm {
    margin-bottom: 0.9rem;
  }

  .mb-md {
    margin-bottom: 1rem;
  }

  .mb-bg {
    margin-bottom: 2.9rem;
  }

  .mr-sm {
    margin-right: 1rem;
  }
}
