.footer {
  background: #12082d;
  padding-top: 70px;
}
.footer a:link,
.footer a:visited {
  color: inherit;
}
.mainDiv {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(239, 240, 242, 0.1);
  padding-bottom: 97px;
}
.footer p {
  font-family: var(--gilroy-semibold);
  font-size: 20px;
  line-height: 26px;
  color: #ffffff;
  padding-bottom: 25px;
}
.footer ul {
  list-style: none;
}
.footer ul li,
.div2 a {
  font-family: var(--gilroy-regular);
  font-size: 18px;
  line-height: 46px;
  color: rgba(255, 255, 255, 0.6) !important;
}

.div2 {
  display: flex;
  justify-content: space-between;
  padding: 60px 0px;
}
.div2 div:last-of-type {
  display: flex;
  justify-content: center;
  align-items: center;
}
.div2 div:last-child a {
  margin-right: 34px;
}
.logoDiv {
  display: flex;
}
.logoDiv div {
  margin-left: 35px;
}

.newsletter{
  width: 35%;
  padding: 1rem;
  margin-top: -1.5rem;
}
.footer button {
  font-size: 0.875rem;
  margin-top: 1.5rem;
  width: 22rem;
  background-color: #eab221;
  border: none;
  color: #fff;
}
.newsletter form button {
  width: 95%;
}
@media screen and (max-width: 1200px) {
  .newsletter {
    width: 99%;
  }
  .mainDiv {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 80px;
    grid-column-gap: 50px;
    justify-content: center;
  }
}
@media screen and (max-width: 646px) {
  .learn {
    display: none;
  }
  .logoDiv {
    flex-direction: column;
    justify-content: flex-start;
    gap: 0px;
  }
  .newsletter {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .newsletter form input {
    width: 45%;
  }
  .div2 {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 12px;
  }
  .div2 div:last-child a {
    margin-right: 15px;
  }
  .logoDiv div {
    margin-left: 0px;
  }
  .mainDiv{
    padding-bottom: 30px;
  }
  .div2{
    padding: 20px 0px;
  }
}
