.container {
  display: flex;
  width: 90%;
  margin: 6.6rem auto auto auto;
}
.hero_bg {
  flex-basis: 45%;
  overflow: hidden;
}
.hero_bg img {
  width: 100%;
}
.landing_div a {
  padding: 1rem 1.5rem;
  border-radius: 5px;
  cursor: pointer;
}
.landingP{
  width: 70%;
}
.switch {
  border: none;
  background: #eab221;
  color: #ffffff;
  margin: 1.9rem 0;
  display: block;
  width: 180px;
  text-align: center;
  border-radius: 5px;
  padding: 15px 20px;
}
.landing_div {
  width: 90%;
  margin: auto;
}
.landing_div h1 {
  margin: 5rem 0 2rem 0;
  font-size: 35px;
  line-height: 42px;
  color: #12082d;
}
.landing_div > p,
.host_review p:first-of-type {
  font-size: 16px;
  line-height: 28px;
  color: #636366;
}
.host_review{
  margin-top: 9rem;
}
.host_name {
  font-style: italic;
  font-weight: 600;
  font-size: 20px;
  letter-spacing: 0.01em;
  color: #eab221;
  margin: 10px 0;
}
.host_name ~ p {
  font-size: 14px;
  color: #9da8b6;
}

@media screen and (max-width: 900px) {
  .container {
    display: flex;
    flex-direction: column-reverse;
  }
  /* .hero_bg img {
 height: 50px;
} */
}
@media screen and (max-width: 600px) {
  .landing_div h1 {
    font-size: 21px;
  }
}
