.community {
  margin-top: 5rem;
  margin-bottom: 3rem;
}
.style {
  box-shadow: 0px 20px 100px rgba(0, 0, 0, 0.05);
  padding: 3rem;
  width: 380px;
  height: 280px;
  margin: 50px 0;
}
.box_div {
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.box {
  width: 95%;
  padding: 3rem;
  margin: auto;
}
.star {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;
}

.star span {
  display: inline-block;
  margin-left: 10px;
}
@media screen and (max-width: 966px) {
  .next_prev {
    display: none;
  }
  .style {
    width: 250px;
  }
}

@media screen and (max-width: 836px) {
  .community {
    width: 90%;
    margin: 5rem auto 7rem auto;
  }
  .box_div {
    width: 100%;
  }
  .box {
    width: 100%;
    padding: 1rem;
  }
  .star > img {
    width: 12px !important;
  }
}
