.header {
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
  z-index: 1000;
  border-bottom: 2px solid rgba(255, 255, 255, 0.18);
}

.header_fixed {
  background-color: white;
  animation: fadeInDown 0.95s ease-in-out 0s 1 normal none running;
  z-index: 999;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  box-shadow: 0 8px 20px 0 rgba(0 0 0 / 10%);
}

.header_fixed svg path {
  fill: #eab221 !important;
}

.header_show svg path {
  fill: #eab221 !important;
}

.header_container {
  width: 85%;
  margin: auto;
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
}

.header_img {
  flex-basis: 300px;
}
.move_items {
  margin-right: auto;
}
.header_buttons {
  display: flex;
}

.header_buttons button:not(:last-of-type) {
  margin-right: 2rem;
}

.header_buttons button:first-of-type {
  transition: 0.3s ease-in-out;
  border: 1px solid #fff;
  color: #fff;
  background-color: transparent;
}
.header_buttons button:last-of-type {
  display: none;
}

.header_fixed button:first-of-type {
  color: #12082d;
  border-color: #12082d;
}

.header_buttons button:first-of-type:hover {
  background-color: #eab221;
  border: 1px solid #eab221;
  color: #fff;
}

.header_fixed .menu svg path {
  fill: #12082d !important;
}

.menu_dark_theme {
  border: 1px solid #fff;
  color: #12082d;
}

.menu_dark_theme svg path {
  fill: #12082d !important;
}
.menu_dark_theme button:first-of-type {
  border-color: #12082d !important;
  color: #12082d !important;
}
.menu {
  display: none;
  position: relative;
  margin-left: 20px;
}
.hidden_menu {
  visibility: hidden;
  opacity: 0;
}

.svg_div:hover ~ .hidden_menu,
.hidden_menu:hover {
  display: block;
}
.hidden_menu {
  background: #f9fafc;
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.05);
  padding: 1rem;
  border-radius: 5px;
  position: absolute;
  right: 2px;
  top: 45px;
  width: 150px;
  transition: 0.8s opacity, 0.7s visibility ease-in-out;
}
.hidden_menu span {
  display: block;
  background: rgba(99, 99, 102, 0.16);
  width: 100%;
  height: 2px;
  margin: 10px 0;
}
.hidden_menu a,
.hidden_menu p {
  color: rgba(18, 8, 45, 0.7);
  margin: 15px 0;
  display: block;
}
.hidden_menu a {
  font-size: 16px;
  font-weight: bolder;
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.items {
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.items p {
  font-size: 1.4rem;
  background-color: #eab221;
  border-radius: 50%;
  margin: 0 3px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px;
  letter-spacing: 2px;
}

.items span,
.items p {
  color: #12082d;
}
.abbr_color {
  color: #fff !important;
}
.header_fixed .abbr_color {
  color: #ffffff !important;
}
.div_color svg path {
  fill: #12082d !important;
}
.item_fixed svg path,
.item_fixed p,
.item_fixed span {
  fill: #12082d !important;
  color: #12082d;
}

.items > div {
  display: flex;
  background-color: #ffffff;
  border-radius: 40px;
  align-items: center;
  padding: 0.3rem;
}

.items > div > svg {
  margin: 0 20px;
  width: 12px;
  height: 10px;
}

.hide {
  background: #f9fafc;
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.05);
  padding: 1rem;
  border-radius: 20px;
  position: absolute;
  right: 0px;
  top: 80px;
  width: 170px;
  visibility: hidden;
  opacity: 0;
  transition: 0.8s opacity, 0.7s visibility ease-in-out;
}
.unhide {
  visibility: visible;
  opacity: 1;
}
.hide ul {
  list-style: none;
  font-size: 16px;
}

.hide > a {
  text-decoration: underline;
  color: #eab221;
  padding-bottom: 15px;
  display: block;
  cursor: pointer;
}

.hide > a,
.hide ul:first-of-type {
  border-bottom: 1px solid #eff2f7;
}

.hide li {
  margin: 10px 0;
  padding: 5px 0;
  display: block;
}

.hide li a {
  color: #12082d;
}

.nameStyle {
  background-color: #eab221 !important;
}
.nameStyle p {
  background-color: #fff !important;
}
@media screen and (max-width: 1412px) {
  .header_img {
    flex-basis: 180px;
  }
}

@media screen and (max-width: 1271px) {
  .header_img {
    flex-basis: 130px;
  }
}

@media screen and (max-width: 1212px) {
  .header nav {
    display: none;
  }

  .menu {
    display: block;
  }

  .menu img {
    transform: scale(1.5, 1.5);
  }

  .header_container {
    justify-content: space-between;
  }
}

@media screen and (max-width: 640px) {
  .header_buttons button:not(:last-of-type) {
    display: none;
  }
  .header_buttons button:last-of-type {
    display: block;
  }
  .header_container {
    padding: 0.9rem 0;
  }

  .header_buttons button {
    padding: 0.8rem 1.2rem;
  }
  .items p {
    width: 40px;
    height: 40px;
    margin: 0px 5px;
    font-size: 1.2rem;
  }
  .items > div {
    padding: 0.3rem 0rem;
  }
}

@media screen and (max-width: 350px) {
  .header_img {
    transform: scale(0.9, 0.9);
  }

  .header_buttons button {
    padding: 0.5rem 0.8rem;
  }
}
