.dealGrid {
  padding-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 265px);
  column-gap: 4.7rem;
  row-gap: 3.75rem;
  margin: auto;
  width: 100%;
  justify-content: space-between;
}

@media screen and (max-width: 1560px) {
  .dealGrid {
    justify-content: center;
  }
}
@media screen and (max-width: 1380px) {
  .dealGrid {
    grid-template-columns: repeat(auto-fill, 265px);
  }
}
