.popup_container {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: transparent;
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  backdrop-filter: blur(50px);
}
.popup_container > div {
  width: 70%;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff;
  padding: 3rem;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.popup_container p {
  font-size: 30px;
  line-height: 44px;
  color: #636366;
}
.btn {
  margin-top: 20px;
  margin-left: auto;
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.btn button {
  border: none;
  padding: 12px 18px;
  color: #fff;
  border-radius: 10px;
}
.btn button:first-of-type {
  background: #12082d;
  margin-right: 30px;
}
.btn button:last-of-type {
  background: #eab221;
}
.loader {
  position: absolute;
  top: 30px;
  right: -65px;
}
@media screen and (max-width: 760px) {
  .popup_container > div {
    padding: 1rem;
    width: 90%;
  }
  .popup_container p {
    font-size: 16px;
    line-height: 24px;
  }
  .btn button {
    padding: 7px 10px;
  }
}
