.book_details {
  cursor: pointer;
  position: relative;
}
.book_details img {
  margin-left: 1rem;
}
.center,
.span_status {
  text-align: center !important;
}
.span_status {
  width: 150px;
  padding: 7px;
  border-radius: 5px;
}
.popup {
  background: #eab221;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  z-index: 1;
  padding: 0.7rem 1.5rem;
  font-family: var(--gilroy-regular);
  width: 12rem;
  color: #fff;
  top: 36px;
  left: -125px;
}
.popup ul {
  list-style: none;
}
.popup ul li {
  margin: 15px 0;
  cursor: pointer;
}
.ldsellipsis {
  display: inline-block;
  position: relative;
  width: 70px;
  height: 10px;
  margin: auto;
}
.ldsellipsis div {
  position: absolute;
  top: 0px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.ldsellipsis div:nth-child(1) {
  left: 8px;
  animation: ldsellipsis1 0.6s infinite;
}
.ldsellipsis div:nth-child(2) {
  left: 8px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(3) {
  left: 32px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(4) {
  left: 56px;
  animation: ldsellipsis3 0.6s infinite;
}
@keyframes ldsellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes ldsellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes ldsellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
