.auth {
  background-color: #28192c;
  min-height: 100vh;
  background-image: url("../../assets//images/authentication/auth_bg.svg");
  background-size: 100%;
}

.auth_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  /* height: 100% */
}

.auth_img {
  align-self: stretch;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  width: 100%;
  padding: 3rem 0;
  display: flex;
  justify-content: center;
}

.auth_box {
  width: 650px;
  margin: 8rem auto;
  max-width: 95%;
  background: rgba(234, 178, 33, 0.8);
  /* backdrop-filter: blur(20px); */
  border-radius: 20px;
  padding: 2rem;
  align-self: flex-start;
}

.auth_form {
  background: #ffffff;
  border-radius: 20px;
  height: 100%;
}

.auth_form_box {
  padding: 3rem 2.5rem;
}
@media screen and (max-width: 700px) {
  .auth_box {
    width: 500px;
    margin: 2rem auto;
    padding: 1rem;
  }
  .auth_img {
    padding: 1rem 0;
  }
  .auth_form_box {
    padding: 3rem 1.8rem;
  }
}
@media screen and (max-width: 560px) {
  .auth_box {
    width: 450px;
  }
}
@media screen and (max-width: 500px) {
  .auth_box {
    width: 400px;
  }
}
@media screen and (max-width: 420px) {
  .auth_box {
    width: 361px;
  }
}
