.nav {
  position: relative;
  margin-right: auto;
}

.nav_ul {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
}

.nav_li {
  margin-right: 3rem;
}

.nav_a {
  font-weight: bolder;
  font-family: var(--gilroy-regular);
  font-size: 18px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
  text-decoration: none;
  color: #12082d;
  padding-bottom: 10px;
  cursor: pointer;
}
.nav_a svg{
  margin-left: 10px;
}
.nav_fixed .nav_ul .nav_li{
  color: #12082d !important;
}
.link_div {
  background: #eab221;
  border-radius: 5px;
  padding: 1rem;
  position: absolute;
  top: 35px;
  right: -33px;
  width: 170px;
  text-align: center;
}
.positioning {
  position: relative;
}
.link_div span {
  background: #c4c4c4;
  margin: 10px 0;
  display: block;
  height: 1px;
  width: 100%;
}
.link_div a {
  display: block;
  color: #fff;
}
