.main {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(34, 34, 34, 0.8);
}
.container {
  position: absolute;
  width: 40%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  padding: 2rem;
  border-radius: 10px;
}
.container p {
  width: 100%;
  text-align: center;
}
.btnDiv {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}
.btnDiv button:last-of-type {
  color: #08a47d;
}
.loaderDiv {
  width: 75px;
  margin: auto;
}
.ldsellipsis {
  display: inline-block;
  position: relative;
  width: 70px;
  height: 20px;
  margin: 12px auto;
}
.ldsellipsis div {
  position: absolute;
  top: 0px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #eab221;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.ldsellipsis div:nth-child(1) {
  left: 8px;
  animation: ldsellipsis1 0.6s infinite;
}
.ldsellipsis div:nth-child(2) {
  left: 8px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(3) {
  left: 32px;
  animation: ldsellipsis2 0.6s infinite;
}
.ldsellipsis div:nth-child(4) {
  left: 56px;
  animation: ldsellipsis3 0.6s infinite;
}
@keyframes ldsellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes ldsellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes ldsellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
@media screen and (max-width: 985px) {
  .container {
    width: 90%;
  }
  .btnDiv {
    flex-direction: column-reverse;
  }
  .btnDiv button {
    text-align: center;
    display: block;
  }
  .btnDiv button:last-of-type {
    margin-bottom: 20px;
  }
}
