.result_container {
  display: flex;
  justify-content: space-between;
}
.result button {
  background-color: transparent;
}
.result_category {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
}

.result_input {
  display: flex;
}
.result_input button {
  background: transparent;
  border: none;
  cursor: pointer;
}
.result_input input {
  border: 1px solid rgba(99, 99, 102, 0.1);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  font-size: 14px;
  color: #636366;
  padding: 0.7rem 1rem;
}
.result_input input:focus {
  outline: 1px solid #ecb321;
}

.result_input button img {
  background: #ecb321;
  border-radius: 0px 8px 8px 0px;
  padding: 5px 10px;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1120px) {
  .result p:last-of-type {
    font-size: 22px;
  }
}
@media screen and (max-width: 480px) {
  .result_container {
    justify-content: center;
  }
  .result_category {
    flex-direction: column;
  }
}
