.img {
  transition: 0.4s ease-in-out;
}

.a:hover .img {
  transform: scale(1.25, 1.12);
  filter: brightness(1.05);
}
.imgDiv {
  width: 290px;
  height: 220px;
  margin-bottom: 10px;
  display: contents;
}
.imgDiv > img {
  border-radius: 10px;
  width: 100%;
  height: auto;
}
.div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 15px;
}
.div:last-of-type {
  padding-bottom: 20px;
  border-bottom: 1px solid #eff0f2;
}
.span {
  color: #636366;
  font-weight: bolder;
  font-size: 15px;
  padding-top: 10px;
}
.place {
  font-family: var(--gilroy-bold);
  font-size: 18px;
  color: #12082d;
  display: inline-block;
  margin-right: auto;
}
.strong {
  font-size: 15px;
  color: #12082d;
  padding-top: 15px;
}
.divs {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: baseline;
}

.status {
  border-radius: 5px;
  width: 120px;
  padding: 7px;
  font-size: 14px;
}
.approved {
  background: #e4f7f3;
  color: #08a47d;
}
.declined {
  background: #ffedec;
  color: #eb6c63;
}
.incomplete {
  background: #cd40ff;
  color: #ffffff;
}
.pending {
  background: #eab221;
  color: #fff;
}

@media screen and (max-width: 520px) {
  .img {
    transform: scale(1, 1);
    filter: brightness(1.1);
    margin-bottom: 15px;
  }
}
