.bookings {
  background: #ffffff;
  border-radius: 20px;
  width: 100%;
  padding: 2rem 3rem;
}
.bookings table {
  width: 100%;
  text-align: left;
}
.bookings > p {
  margin: 2rem 0;
  font-family: var(--gilroy-medium);
  font-size: 16px;
  color: #12082d;
}
.bookings table th{
  color: #9DA8B6;
}
.bookings table th:last-of-type,
.bookings table td:last-of-type{
  width: 20%;
}
.bookings table td{
  padding: 20px 0;
  color: #12082D;
  font-weight: bolder;
}
.success_text{
    background: #E4F7F3;
    border-radius: 5px;
    color: #08A47D;   
    padding: 10px 15px;
}
.failed_text{
    color: #EB6C63;
    background: #FFEDEC;
    padding: 10px 15px;
    border-radius: 5px;
}
.mobile{
  display: none;
  /* margin: 9rem auto; */
}
@media screen and (max-width: 990px) {
  .bookings{
    padding: 1rem 2rem;
  }  
  .bookings table{
        display: none;
        /* padding: 0 100px; */
    }
    .mobile{
        display: block;
        /* padding: 0 50px; */
    }
    .bookings > p{
      margin: 2rem 0.5rem;
    }
}