.result_container {
  display: flex;
  justify-content: space-between;
}

.result_category {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
}

.result_input {
  display: flex;
}

.result_input input {
  border: 1px solid rgba(99, 99, 102, 0.1);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  font-size: 14px;
  color: #636366;
  padding: 0.7rem 1rem;
}
.result_input input:focus {
  outline: 1px solid #ecb321;
}

.result_input button {
  width: 40px;
  background: #ecb321;
  border: none;
  border-radius: 0px 8px 8px 0px;
}

.result_input div img {
  padding: 5px;
  width: 100%;
  height: 100%;
}
.div2 {
  display: none;
}
.result_category span {
  transition: 0.35s linear;
}

@media screen and (max-width: 1120px) {
  .result_category span {
    display: none;
  }
  .div2 {
    display: flex;
  }
  .result p:last-of-type {
    font-size: 22px;
  }
}
@media screen and (max-width: 370px) {
  .result_input input {
    width: 140px;
  }
}
