.login_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}
.or_container{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}
.or_container span{
  display: block;
  width: 40%;
  flex-basis: 45%;
  height: 2px;
  background: #F1F6F8;
}
.login p {
  text-align: center;
  font-size: 1.2rem;
  flex-grow: 1;
}
.spa{
  font-style: normal;
  font-weight: lighter;
  color: #12082d;
  font-size: 20px;
line-height: 27px;
  }
 
@media (max-width: 1200px) {
  .login_header {
    align-items: initial;
    flex-direction: column;
  }
  
}
@media screen and (max-width: 456px){
  .spa{
    font-size: 12px;
    line-height: 16px;
      }
}
@media screen and (max-width: 377px){
  .spa{
    font-size: 10px;
    line-height: 13px;
   
  }
  .login p{
    font-size: 0.7rem;
  }
 
}