.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container p,
.container a {
  display: block;
  text-align: center;
}
.container p {
  width: 50%;
  margin: 30px auto;
  font-size: 20px;
  color: #636366;
}
.container a {
  width: 40%;
  margin: auto;
  background: #eab221;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 20px;
  color: #fff;
}
@media screen and (max-width: 660px) {
  .container p {
    font-size: 15px;
    width: 90%;
  }
}
