.container {
  width: 85%;
  margin: 6.5rem auto auto auto;
}
.container > div {
  padding: 2rem;
  border-radius: 20px;
  background: #ffffff;
}
.greenText,
.status span:first-of-type {
  color: #39d176;
}

.container table {
  margin-top: 2rem;
  width: 50%;
  text-align: left;
  font-size: 14px;
  position: relative;
}
.mainText span:first-of-type {
  color: #eab221;
  margin-right: 25px;
}
.mainText,
.subText {
  font-weight: bolder;
  font-size: 24px;
}
.mainText {
  padding: 1.2rem 0;
}
.subText {
  color: rgba(18, 8, 45, 0.9);
  padding: 1.2rem 0;
}
.container table th {
  font-weight: bolder;
  color: #eab221;
}
.container table tr td {
  padding: 20px 0;
}
.mobile {
  display: none;
}
.mobile .house {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.house span {
  font-size: 14px;
}
.status {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mob {
  margin-bottom: 20px;
  border: 1px solid #e5e9f2;
  border-radius: 5px;
  padding: 10px;
}
.status span:first-of-type {
  background: rgba(57, 209, 118, 0.15);
  border-radius: 5px;
  padding: 5px 10px;
}
@keyframes ldio-gr8jl2w6xgv {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.ldio_gr8jl2w6xgv div {
  width: 35px;
  height: 35px;
  border: 3px solid #eab221;
  border-top-color: transparent;
  border-radius: 50%;
}
.ldio_gr8jl2w6xgv div {
  animation: ldio-gr8jl2w6xgv 0.6666666666666666s linear infinite;
  top: 30px;
  left: 30px;
  position: absolute;
}
.loadingio_spinner_rolling_a2f6fu9hy66 {
  width: 70px;
  height: 70px;
  position: absolute;
  top: 85px;
  left: 45%;
  display: inline-block;
  overflow: hidden;
}
.ldio_gr8jl2w6xgv {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0;
}
.ldio_gr8jl2w6xgv div {
  box-sizing: content-box;
}

@media screen and (max-width: 900px) {
  .content {
    display: none;
  }
  .mobile {
    display: block;
    margin-top: -71px;
  }
  .mainText,
  .subText {
    font-size: 16px;
  }
  .house span:first-of-type {
    color: rgba(18, 8, 45, 0.9);
    margin-bottom: 20px;
  }
  .location {
    color: #909198;
  }
}
