.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  z-index: 100000;
  /* backdrop-filter: blur(10px); */
  overflow-y: auto;
  background: rgb(18 8 45 / 53%);
}

.modal_container {
  min-height: 100vh;
  display: flex;
}

.modal_box {
  width: 700px;
  margin: 6rem auto;
  background: rgba(255, 255, 255, 0.1);
  /* backdrop-filter: blur(20px); */
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 20px;
  padding: 3rem;
  align-self: flex-start;
}

.modal_form {
  background: #ffffff;
  box-shadow: 0px 5px 40px 1px rgba(106, 67, 103, 0.12);
  border-radius: 20px;
  height: 100%;
  position: relative;
}

.span {
  position: absolute;
  right: 20px;
  background-color: #eab221;
  top: 20px;
  padding: 10px;
  border-radius: 50%;
  height: 30px;
  width: 30px;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: white;
  cursor: pointer;
}

.modal_form_box {
  padding: 3rem 2.5rem;
}
@media screen and (max-width: 690px){
  .modal_box{
    padding: 1.5rem
  }
  
}