.shrink > div{
  width: auto !important; 
  height: 100vh !important;
  z-index: 100 !important;
  overflow: hidden;
} 
.property_name,
.suggestion_div input,
.options > div {
  padding: 1.2rem;
  width: 80%;
  border: 1px solid #eff2f7;
  border-radius: 10px;
  font-size: 16px;
  color: #9da8b6;
  font-family: var(--gilroy-regular);
}
.suggestions {
  position: relative;
}
.options > div {
  position: absolute;
  top: 60px;
  left: 0px;
  z-index: 1;
  background-color: #fff;
  height: 300px;
  overflow: auto;
}
.options p {
  padding: 10px 0;
}
.form label {
  margin-top: 30px;
}
.labelflex {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.labelflex button {
  margin-left: 20px;
}
.property_name:focus,
.textbox textarea:focus {
  outline: 2px solid #eab221;
}
.amenities_div > div {
  width: 70%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.amenities_div,
.rules_div {
  padding-top: 2.5rem;
  color: #636366;
}
.amen_label {
  font-size: 17px;
  cursor: pointer;
  position: relative;
  padding-left: 15px;
}
.amen_label input {
  margin-right: 12px;
  background: #eab221;
  border: 0.5px solid rgba(99, 99, 102, 0.6);
  width: 0px;
  height: 0px;
  opacity: 0;
}
.googlePlaces {
  width: 80%;
}
.googlePlaces > div > div {
  border-radius: 10px;
  padding: 0.8rem;
  border: 1px solid #eff2f7;
}
.amen_label span {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  border: 0.5px solid rgba(99, 99, 102, 0.6);
}
.amen_label span:after {
  content: "";
  position: absolute;
  display: none;
}
.amen_label span:after {
  left: 6px;
  top: 1px;
  width: 4px;
  height: 9px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.amen_label input:checked ~ .amen_span:after {
  display: block;
}
.amen_label input:checked ~ .amen_span {
  background-color: #eab221;
}
.textbox textarea {
  width: 80%;
  height: 216px;
  background: rgba(99, 99, 102, 0.02);
  border: 1px solid rgba(99, 99, 102, 0.34);
  border-radius: 10px;
  padding: 2rem;
  font-family: var(--gilroy-regular);
  font-size: 14px;
}
.upload {
  width: 335px;
  height: 300px;
  /* background: #ffffff; */
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  background-image: url("../../assets/images/new_listing/upload_image.svg");
  border: 1px solid rgba(99, 99, 102, 0.34);
  border-radius: 10px;
  font-size: 13px !important;
  line-height: 24px;
  letter-spacing: -0.4px;
  color: rgba(99, 99, 102, 0.98);
  padding: 2rem 1.5rem;
  text-align: center;
  cursor: pointer;
  position: relative;
}
.upload1 {
  position: relative;
}
.images {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.upload > ul,
.upload li {
  padding: 0.3rem 0;
}
.upload > input {
  width: 0px;
  height: 0px;
}
.upload_span {
  text-align: center;
  display: block;
  background: #eab221;
  border-radius: 5px;
  color: #fff;
  width: 70%;
  margin: 0px auto;
}
.upload > img {
  width: 95%;
}
.t_and_c {
  display: flex !important;
  font-size: 14px;
  line-height: 18px;
  color: rgba(99, 99, 102, 0.6);
}
.t_and_c input {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.form_btn button {
  margin-top: 6rem;
  margin-right: 3.4rem;
  width: 200px;
  height: 45px;
  padding: 20px 0;
  border-radius: 13.0804px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form_btn button:first-of-type {
  background: #ffffff;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
  color: #282829;
  border: 1px solid #12082d;
}
.form_btn button:last-of-type {
  background: #eab221;
  color: #fff;
  border: 1px solid #eab221;
}
.amountDiv {
  position: relative;
}
.amountDiv input {
  padding-left: 3rem;
}
.amountDiv span {
  position: absolute;
  top: 15px;
  left: 20px;
  font-size: 1.5rem;
  color: #9da8b6;
}
.grid_imgs {
  display: flex;
  flex-wrap: wrap;
  margin-right: 10%;
}
.grid_imgs img {
  width: 250px;
  margin: 20px 10px;
}
.clear {
  background-color: #eab221;
  color: #fff;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 10px;
  display: block;
  margin: auto;
  width: 120px;
}
.progressBar {
  width: 50%;
  height: 20px;
  border: 1px solid #12082d;
  background: transparent;
  border-radius: 10px;
}
.progressBar div {
  border-radius: 10px;
  height: 100%;
  background: #eab221;
  transition: width 0.5s ease-in-out;
}
@media screen and (max-width: 900px) {
  .amenities_div > div {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 600px) {
  .property_name {
    font-size: 13px;
  }
  .textbox textarea {
    font-size: 13px;
  }
  .upload {
    width: 100%;
  }
  .upload_span {
    margin-top: -25px;
  }
}
