.book_details {
    cursor: pointer;
    position: relative;
  }
  .book_details img {
    margin-left: 1rem;
  }
  .span_status {
    text-align: left;
    width: 100px;
    padding: 7px;
    border-radius: 5px;
  }
  .popup {
    background: #eab221;
    border-radius: 5px;
    text-align: center;
    position: absolute;
    z-index: 1;
    padding: 0.7rem 1.5rem;
    font-family: var(--gilroy-regular);
    width: 12rem;
    color: #fff;
    cursor: default;
  }
  .popup ul {
    list-style: none;
  }
  .popup ul li {
    margin: 20px 0;
    cursor: pointer;
  }
  