.location {
  width: 90%;
  height: 90vh;
  margin: 6.5rem auto 0 auto;
  position: relative;
  border-radius: 50px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.location h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}
.location > .availability button {
  padding: 1rem;
}
.availability {
  position: absolute;
  bottom: -60px;
  left: -20px;
  width: 105%;
}

.section {
  margin: 10rem auto 5rem auto;
}

.filters {
  margin: 3rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.filters span {
  margin-right: 2rem;
}

.section button {
  padding: 20px 00px;
  width: 130px;
  margin: 80px auto;
  display: block;
  background-color: transparent;
  border: 1px solid #c0ccda;
  border-radius: 5px;
  font-size: 14px;
  letter-spacing: 0.01em;
  color: #636366;
}
@media (max-width: 892px) {
  .filters div span:not(:first-child, :nth-child(2), :nth-child(3)) {
    display: none;
  }
}
@media (max-width: 640px) {
  .location {
    height: 70vh;
  }
  .filters div span:not(:first-child) {
    display: none;
  }
}
