.shrink > div {
  width: auto !important;
  height: auto !important;
  z-index: 0 !important;
}
.dealGrid {
  padding-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 285px);
  column-gap: 4.7rem;
  row-gap: 3.75rem;
  margin: auto;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.show {
  background:linear-gradient(rgba(0, 0, 0, 0.8), rgb(0, 0 , 0, 0.8)),
  url(https://res.cloudinary.com/drsimple/image/upload/v1633342872/mw95ex7prknqvxmp37gi.jpg);
  background-size: 100%;
  width: 271px;
  height: 202px;
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
  border-radius: 10px;
  color: #ffffff;
  margin-bottom: 97px;
}

@media screen and (max-width: 1560px) {
  .dealGrid {
    justify-content: center;
  }
}
@media screen and (max-width: 1380px) {
  .dealGrid {
    grid-template-columns: repeat(auto-fill, 265px);
  }
}
