.container {
  position: fixed;
  top: 0;
  left:0;
  width: 100vw;
  height: 100vh;
  background: rgba(99, 99, 102, 0.29);
  backdrop-filter: blur(10px);
  z-index: 1000000;
}
.content {
  position: fixed;
  background: #ffffff;
  width: 50%;
  /* max-width: 50%; */
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 50px;
  align-items: center;
  justify-content: center;
  padding: 4.5rem 3.5rem;
}
.head {
  display: flex;
  justify-content: space-between;
}
.head p:first-of-type {
  color: rgba(99, 99, 102, 0.9);
  font-size: 25px;
}
.head span {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  top:20px;
  right: 38px;
}
.copy {
  background: rgba(99, 99, 102, 0.6);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  gap: 2px;
  padding: 10px 20px;
  color: #ffffff;
  align-items: center;
  border: none;
  cursor: pointer;
}
.check {
  background: #39d176;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
}
.tall {
  /* display: flex; */
  /* flex-direction: column; */
  padding: 2rem 0;
}
.belt{
  display: flex;
  justify-content: space-between;
}
.site {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
}

