  background: rgba(99, 99, 102, 0.05);
  border-radius: 10px;
  width: 19rem;
  padding: 12px 0px 12px 20px;
  margin: 5px 0px 30px 0px;
} */
.belt span {
  background: rgba(99, 99, 102, 0.05);
  border-radius: 10px;
  width: 15rem;
  padding: 12px 0px 12px 12px;
  margin: 5px 0px 30px 0px;
}
.site p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.4px;
  color: rgba(99, 99, 102, 0.9);
}
.belt p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.4px;
  color: rgba(99, 99, 102, 0.9);
}
.toll button {
  border: none;
  border-radius: 10px;
  color: #ffffff;
  cursor: pointer;
  width: 140px;
  padding: 12px 2px;
}
.toll button:not(:last-of-type) {
  margin-right: 3rem;
}
.toll button:first-of-type {
  background: #12082d;
}
.toll button:nth-of-type(2) {
  background: #979797;
  cursor: not-allowed;
}
.toll button:nth-of-type(3) {
  background: #7a1f1f;
}
.toll button:last-of-type {
  background: #eab221;
}
.toll {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}



@media screen and (max-width: 900px){
  .container{
    top: 20%
  }
  .content{
    width: 50%;
  }
  .check{
    display: none;
  }
  .head p:first-of-type {
  font-size: 15px;
}
  /* .side{
    display: grid;
    grid-template-rows: repeat(1, 1fr);
  } */
  .copy {
 
  padding: 10px 10px;
  
}

  .belt span {
    width: 10rem;
    font-size: 10px;
  }
   
  .toll{
    display: grid;
    grid-template-columns: repeat(2, 2fr);
    grid-gap: 20px;
  }
  .belt:nth-of-type(2){
    display: flex;
    flex-direction: column;
  }
  .belt:last-of-type{
    display: flex;
    flex-direction: column;

  }
  .content{
    padding: 3.5rem 1.5rem;
    width: 50%;
  }
  .site p {
  font-size: 11px;
  }
}

@media screen and (max-width: 500px){
  .head p:first-of-type {
  font-size: 10px;
}
   .site p {
  font-size: 8px;
  }
   .belt span {
    width: 9rem;
  }
  .content{
    width: 73%;
  }
}

@media screen and (max-width: 550px){
  .content{
    width: 88%;
  }
}
@media screen and (max-width: 450px){
  .content{
    width: 88%;
  }
}
@media screen and (max-width: 375px){
  .belt span {
    width: 7rem;
  }
}
