.grid {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 30px;
}

.grid div {
  display: flex;
  align-items: center;
}

.grid div span{
  margin-left:1rem
}

.amenities p {
  line-height: 2.5 !important
}
.amenities{
  width: 100%;
  max-width: 650px;
}
.btn_show{
  font-size: 14px;
  color: rgba(99, 99, 102, 0.8);
  background: transparent;
  border: 0.8px solid rgba(99, 99, 102, 0.7);
  border-radius: 10px;
  padding: 10px 20px;
  margin: 30px auto;
  width: 100%;
  cursor: pointer;
}
@media screen and (max-width:550px){
  .grid{
    grid-template-columns: auto auto;
  }
}