.showmore {
    padding: 20px;
    /* margin: 50px auto; */
    display: block;
    background-color: transparent;
    border: 1px solid #c0ccda;
    border-radius: 5px;
    font-size: 14px;
    letter-spacing: 0.01em;
    color: #636366;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.showmore img {
    margin-right: .5rem;
}