.container {
  width: 100vw;
  height: 0vh;
  transition-property: all;
	transition-duration: .8s;
	transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  left: 0;
  bottom: -100%;
  text-align: left;
  background: rgba(18, 8, 45, 0.6);
  position: absolute;
}
.animate{
  bottom: 0%;
  height: 100vh;
  
}
.container > div {
  width: 100%;
  height: 75%;
  padding: 0.5rem 2rem;
  border-radius: 30px 30px 0px 0px;
  background: #fff;
  position: absolute;
  bottom: 0rem;
}
.container span {
  font-size: 2.5rem;
  display: block;
}
.container p:first-of-type {
  font-size: 1.2rem;
}
.btn{
    width: 100%;
    background: #eab221;
    border: none;
    padding: 0.8rem;
    border-radius: 10px;
    color: #fff;
    font-size: 15px;
}