.boxshadow {
  width: 700px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 3rem;
  align-self: flex-start;
}

.shadow_container {
  background: #ffffff;
  box-shadow: 0px 5px 40px 1px rgba(106, 67, 103, 0.12);
  border-radius: 20px;
  height: 100%;
}
@media screen and (max-width: 795px) {
    .boxshadow{
        width: 95%;
        margin: auto;
        padding: 1.8rem;
    }
}