.goog {
  background: #ffffff;
  /* border: 1px solid #12082d; */
  border: 1px solid rgba(18, 8, 45, 0.8);
  box-sizing: border-box;
  border-radius: 10px;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  margin-top: 1rem;
}
.goog > button{
  box-shadow: none !important;
  font-size: 19px !important;
  color: rgba(18, 8, 45, 0.8) !important;
  background: #fff;
  display: flex;
  align-items: center;
  border: none !important;
  /* padding: 10px 100px !important; */
}
/* .goog > a{
  text-decoration: none;
  display: flex;
  align-items: center;
  box-shadow: none !important;
  font-size: 19px !important;
  color: rgba(18, 8, 45, 0.8) !important;
  border: none;
} */
.goog > button > svg{
  transform: scale(1.5);
}
.goog > button > img{
  transform: scale(0.6);
  display: block;
}
.goog > button > span{
  display: block;
}
@media screen and (max-width: 628px) {
  .goog > button {
    /* padding: 10px; */
    font-size: 17px !important;
    height:47px;
  }
  .goog > button > span{
    font-size: 17px;
  }
}
@media screen and (max-width: 459px) {
  .goog > button {
    /* padding: 10px; */
    font-size: 15px !important;
   
  }
  .goog{
     height:50px;
  }
  .goog > button > span{
    font-size: 15px;
  }
}

@media screen and (max-width: 380px) {
  .goog > button {
    font-size: 12px !important;
   
  }
  .goog{
     height:49px;
  }
  .goog > button > span{
    font-size: 12px;
  }
}
@media screen and (max-width: 320px) {
  .goog > button {
    font-size: 10px !important;
   
  }
  .goog{
     height:49px;
  }
  .goog > button >span{
    font-size: 10px;
  }
}