.hostSection {
  position: relative;
  background-image: url("../../../assets/images/landing/hostbg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.alink{
  background: #eab221;
  padding: 15px 30px;
  border-radius:10px;
  color: #fff;
}
.host_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6rem;
}
@media screen and (max-width: 768px) {
  .hostSection {
    height: 75vh;
  }
  .host_content {
    padding: 2rem 1rem;
  }
}
