.main_container{
    padding-top: 8rem;
    width: 90%;
    margin: auto;
    background: rgba(255, 255, 255, 0.88);
    padding-bottom: 7rem;
}
.main_container> p{
    font-size: 20px;
    color: #12082D;
    margin: 2rem 0;
    font-weight: bolder;
}
.form_container {
    font-family: var(--gilroy-regular);
    background: #fff;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
    border-radius: 26.1608px;
    width: 100%;
    /* padding-left: 3rem;
    padding-top: 3rem;
    padding-bottom: 6rem; */
    padding: 3rem 2rem 6rem 2rem;
  }
  .form_container p {
    font-size: 20px;
    color: rgba(18, 8, 45, 0.7);
    font-weight: bolder;
  }
  
  .form_container label {
    font-size: 15px;
    color: #636366;
    margin-top: 10px;
    display: block;
  }

