.container {
  margin-top: 9rem;
  position: relative;
  background: rgb(247, 248, 250);
}

.bookings_section table td,
.bookings_section table th {
  text-align: left;
}
.btn {
  margin-top: 4rem;
  display: flex;
  justify-content: flex-end;
}
.btn button {
  color: #ffffff;
  border-radius: 10px;
  padding: 1rem 2rem;
  border: none;
}
.btn button:first-of-type {
  background: #12082d;
  margin-right: 20px;
}
.btn button:last-of-type {
  background: #eab221;
}

.bookings_section {
  background: #ffffff;
  border-radius: 20px;
}
.bookings_section table {
  width: 100%;
  padding: 30px;
}

.bookings_section table td {
  border-bottom: 2px solid rgba(99, 99, 102, 0.2);
  color: #12082d;
  padding: 20px 0;
}
.bookings_section table tr {
  padding: 20px 0;
}
.bookings_section table th {
  font-size: 16px;
  color: #eab221;
}
.showmore {
  padding: 1rem 0;
  background: rgb(247, 248, 250);
}
.mobile {
  display: none;
  margin: 9rem 0;
}
td[type="active"],
p[type="active"] {
  color: #39d176 !important;
}
td[type="inactive"],
p[type="inactive"] {
  color: #f33737 !important;
}

@media screen and (max-width: 990px) {
  .bookings_section {
    display: none;
  }
  .mobile {
    display: block;
    padding: 0 50px;
  }
}
@media screen and (max-width: 600px) {
  .btn {
    display: flex;
    justify-content: center;
  }
  .popup_container p {
    font-size: 20px;
  }
}
