.hostSection {
    position: relative;
    background-image: url("../../../assets/images/landing/hostbg.jpg");
    background-repeat: no-repeat;
  background-size: cover;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.host_content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem 6rem;
}
.host_content a{
    background: #eab221;
    color: #fff;
    padding: 12px 20px;
    border-radius: 5px;
}
@media screen and (max-width: 768px) {
    .hostSection {
        height: 75vh;
    }
    .host_content{
        padding: 2rem 1rem;
    }
    
}
