.title {
  font-style: normal;
  font-weight: bold;
  color: #ffffff;
}

.title-bg {
  font-size: 4.375rem;
}

.title-sm {
  font-size: 2.5rem;
}

.subtitle {
  font-style: normal;
  font-weight: bolder;
  color: #12082d;
}

.subtitle-bg {
  font-size: 2.5rem;
}

.subtitle-md {
  font-size: 2rem;
  text-align: center;
}

.subtitle-sm {
  font-size: 1.25rem;
}

.text {
  font-size: 1rem;
  line-height: 1.5;
}

.text-26 {
  font-size: 1.35rem !important;
  margin-bottom: 0.8rem;
}
/* .text-20 {
  font-size: 1.25rem !important;
  font-weight: 500;
}
.text-20 {
  font-style: normal;
  font-weight: 500;
  color: #12082d;
} */
.text-12 {
  font-size: 0.75rem;
}

.text-14 {
  font-size: 0.875rem;
}
.text-13 {
  font-size: 0.875rem;
  margin-top: 0.8rem;
  /* width: 280px; */
  position: relative;
}

.text-dark {
  color: #858590;
}

.text-yellow {
  color: #eab221;
}

.text-purple {
  color: #12082d;
  font-weight: 700;
}

.button_bg {
  background: #eab221;
  border: none;
  color: #ffffff;
}

.button_bd {
  border: 1px solid #12082d !important;
  color: #12082d !important;
  font-size: 14px;
  background-color: transparent;
}

.cursor {
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .title-bg {
    font-size: 2.3375rem;
  }
  .subtitle-bg {
    font-size: 1.5rem;
  }
  .text-20 {
    font-size: 1.2rem;
  }
  .text {
    font-size: 0.85rem;
  }
  .subtitle-md {
    font-size: 1.6rem;
  }
  .title-sm {
    font-size: 1.4rem;
  }
}
@media screen and (max-width: 456px) {
  .text-26 {
    font-size: 1.3rem !important;
    line-height: 19px;
    font-size: 1rem
  }
  .text-14 {
    font-size: 0.9rem;
  }
  .text {
    font-size: 1rem;
  }
}
