.div {
  display: flex;
  width: 240px;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0 1rem;
}
.div div {
  margin-bottom: 2rem;
  background-color: #eab221;
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
}
.div img {
  width: 20px;
  height: 20px;
}

.div p {
  line-height: 30px;
  letter-spacing: 0.02em;
}
