.header {
  position: fixed;
  padding: 0.8rem 0;
  top: 0;
  width: 100%;
  left: 0;
  z-index: 1000;
  border-bottom: 2px solid rgba(99, 99, 102, 0.08);
  background-color: #ffffff;
}

.header_fixed {
  animation: fadeInDown 0.95s ease-in-out 0s 1 normal none running;
  z-index: 999;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  box-shadow: 0 8px 20px 0 rgba(0 0 0 / 10%);
}
.authDiv {
  padding: 1.8rem 0;
}
.header_fixed svg path {
  fill: #eab221 !important;
}

.header_show svg path {
  fill: #eab221 !important;
}

.header_container {
  width: 85%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header_img {
  flex-basis: 300px;
}
.move_items {
  margin-right: auto;
}
.header_buttons {
  display: flex;
}

.header_buttons button:not(:last-of-type) {
  margin-right: 2rem;
}

.header_buttons button:first-of-type {
  transition: 0.3s ease-in-out;
  background-color: transparent;
  color: #12082d;
  border: 1px solid #12082d;
}
.header_buttons button:last-of-type {
  display: none;
}

.header_fixed button:first-of-type {
  color: #12082d;
  border-color: #12082d;
}

.header_buttons button:first-of-type:hover {
  background-color: #eab221;
  border: 1px solid #eab221;
  color: #fff;
}

.header_fixed .menu svg path {
  fill: #12082d !important;
}

.menu_dark_theme {
  border: 1px solid #fff;
  color: #12082d;
  margin-right: 20px;
}

.menu_dark_theme svg path {
  fill: #12082d !important;
}
.menu_dark_theme button:first-of-type {
  border-color: #12082d !important;
  color: #12082d !important;
}
.menu {
  margin-left: 5px;
  position: relative;
}
.hidden_menu {
  visibility: none;
  opacity: 0;
  transition: 0.8s opacity, 0.7s visibility ease-in-out;
}
.hidden_menu span {
  width: 100%;
  height: 1px;
  display: block;
  background: rgba(18, 8, 45, 0.7);
}

.hidden_menu {
  background: #f9fafc;
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.05);
  padding: 0 1rem;
  border-radius: 5px;
  position: absolute;
  right: -20px;
  top: 25px;
  width: 150px;
}
.hidden_menu p {
  color: rgba(18, 8, 45, 0.7);
  margin: 15px 0;
}
.alink1 {
  background: #eab221;
  color: #fff;
}
.alink1,
.alink2 {
  padding: 15px 30px;
  border-radius: 5px;
  margin-right: 20px;
}
.alink2 {
  background: transparent;
  color: #12082d;
  border: 1px solid #12082d;
}
.alink2:hover {
  background: #eab221;
  color: #fff;
  border: 1px solid #eab221;
}
.hidden_list,
.hidden_menu_div {
  display: none;
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

@media screen and (max-width: 1412px) {
  .header_img {
    flex-basis: 180px;
  }
}

@media screen and (max-width: 1271px) {
  .header_img {
    flex-basis: 130px;
  }
}

@media screen and (max-width: 1212px) {
  .header nav {
    display: none;
  }
  .hidden_list {
    display: block;
  }
}

@media screen and (max-width: 640px) {
  .header_buttons {
    display: none;
  }
  .hidden_menu_div {
    display: block;
  }

  .menu img {
    transform: scale(1.5, 1.5);
  }
}

@media screen and (max-width: 350px) {
  .header_img {
    transform: scale(0.9, 0.9);
  }
}

.items {
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.items p {
  font-size: 1.4rem;
  background-color: #eab221;
  border-radius: 50%;
  margin: 0 15px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.items span,
.items p {
  color: #12082d;
}
.abbr_color {
  color: #fff !important;
}
.header_fixed .abbr_color {
  color: #ffffff !important;
}
.div_color svg path {
  fill: #12082d !important;
}
.item_fixed svg path,
.item_fixed p,
.item_fixed span {
  fill: #12082d !important;
  color: #12082d;
}

.items > div {
  display: flex;
  background-color: #ffffff;
  border-radius: 40px;
  align-items: center;
  padding: 0.5rem;
}

.items svg {
  width: 24px;
  height: 24px;
  margin: 0 20px;
}

.items > div > svg {
  width: 12px;
  height: 10px;
}

.hide {
  background: #f9fafc;
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.05);
  padding: 1rem;
  border-radius: 20px;
  position: absolute;
  right: 4px;
  display: flex;
  flex-direction: column;
  top: 72px;
  width: 170px;
  visibility: hidden;
  opacity: 0;
  transition: 0.9s opacity, 0.7s visibility ease-in-out;
}

.hide a {
  color: #12082d;
  font-size: 16px;
  list-style: none;
  margin: 10px 0;
  padding: 5px 0;
}
.hide a:first-of-type {
  color: #eab221;
}
.hide > span {
  text-decoration: underline;
  padding-bottom: 15px;
  display: block;
  cursor: pointer;
  color: #f61f1f;
  cursor: pointer;
}

.hide > span,
.hide a:first-of-type {
  border-bottom: 1px solid #eff2f7;
}

.unhide {
  visibility: visible;
  opacity: 1;
}
