.main {
  position: relative;
  font-family: var(--gilroy-regular);
}
.text {
  color: #bdbdbd;
  font-weight: bolder;
  font-size: 24px;
}
.text span {
  color: #000000;
  margin-right: 10px;
}
.container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  background: #fff;
  z-index: 1000;
  align-items: center;
  justify-content: center;
  display: flex;
}
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 50vh; */
}
.text_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 18rem;
  margin-right: 6rem;
  /* align-items: center; */
}
.btn {
  background: #eab221;
  border-radius: 5px;
  width: 160px;
  color: #fff;
  padding: 1rem 1.5rem;
  font-size: 18px;
  transition: 0.45s ease-in-out;
}
.btn:hover {
  background-color: #000000;
}
.mobile{
  display: none;
}
.logos{
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}
.new{
  display: flex;
  justify-content: center;
  align-items: center;
}
.link{
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

@media screen and (max-width: 950px){
  .box{
    display: none;
  }
  .logos >img{
    width: 10%;
  }
  .mobile{
    display: block;
    padding: 100px;
  }
  .text{
    font-size: 18px
  }
}
@media screen and (max-width: 480px){
 
   .img >img{
    width: 100%;
  }
  .text{
    font-size: 12px;
  }
  .btn{
    width: 130px;
    font-size: 12px;
  padding: 1rem 1rem;
  text-align: center;

  }
}

