.new{
  display:flex;
  flex-direction: column;
  align-items: flex-end;
}
.aside {
  background: #ffffff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 30px;
  margin-top: 40px;
  width: 63%;
}

.span1 {
  font-family: var(--gilroy-bold);
  font-size: 20px;
  color: #323232;
}
.span2 {
  font-family: var(--gilroy-regular);
  font-size: 14px;
  color: #323232;
}
.nots{
  background: rgba(234, 178, 33, 0.05);
  border-radius: 10px;
  display: flex;
  justify-content:space-between;
  align-items: center;
  gap: 30px;
  padding: 30px;
  width: 63%;
  margin-top: 20px;
}
.para{
  font-family: var(--gilroy-bold);
  font-style: normal;
  font-weight: 600;
  font-size: 23px;
  color: #FFBB0B;
}
.het{
  font-family: var(--gilroy-regular);
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 31px;
  color: #222222;
}
@media screen and (max-width: 972px){
  .new{
    display:flex;
    align-items: center;
  }
  .aside{
    width: 80%;
    margin-top:0px,

  }
  .nots{
    width: 80%;
    gap: 15px;
    padding: 10px;
  }
  .het{
    font-size: 12px;
    line-height: 18px;
  }
  .para{
    font-size:15px;
  }
}
