* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  width: 100%;
}

@font-face {
  font-family: "gilroy-regular";
  src: url("./assets/fonts/gilroy/Gilroy-Regular.otf");
}

@font-face {
  font-family: "gilroy-medium";
  src: url("./assets/fonts/gilroy/Gilroy-Medium.otf");
}

@font-face {
  font-family: "gilroy-bold";
  src: url("./assets/fonts/gilroy/Gilroy-Bold.otf");
}

@font-face {
  font-family: "gilroy-semibold";
  src: url("./assets/fonts/gilroy/Gilroy-Semibold.otf");
}

@font-face {
  font-family: "recoleta-alt";
  src: url("./assets/fonts/recoleta/Recoleta-Bold.ttf");
}
@import "~react-image-gallery/styles/css/image-gallery.css";
:root {
  --gilroy-regular: gilroy-regular;
  --gilroy-medium: gilroy-medium;
  --gilroy-bold: gilroy-bold;
  --gilroy-semibold: gilroy-semibold;
  --recoleta: recoleta-alt;
  --gold: #eab221;
  --black: #12082d;
  --grey: #636366;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--recoleta);
}

p,
a,
span,
button,
label,
.css-2b097c-container,
table {
  font-family: var(--gilroy-regular);
}
div[type="success"] {
  background: #e4f7f3;
  color: #08a47d;
}
div[type="pending"] {
  background: rgba(234, 178, 33, 0.8);
  color: #fff;
}

a {
  text-decoration: none;
}

.Lagos {
  background-image: url("./assets/images/landing/lagos.webp");
}

.Oyo {
  background-image: url("./assets/images/landing/oyo.webp");
}

.Abuja {
  background-image: url("./assets/images/landing/abuja.webp");
}

.Ogun {
  background-image: url("./assets/images/landing/ogun.webp");
}
.All-Locations {
  background-image: url("https://images.unsplash.com/photo-1610818647551-866cce9f06d5?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=870&q=80");
}

.current {
  border-bottom: 3px solid #eab221;
}

@media screen and (max-width: 768px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: lighter !important;
  }
}

.Toastify__toast {
  background: 0px 5px 10px rgba(0, 0, 0, 0.05) !important;
  padding: 20px !important;
  border-radius: 10px !important;
}

.Toastify__toast-body {
  font-size: 1rem !important;
}

.Toastify__toast--info {
  background-color: #444854 !important;
}

.Toastify__toast-container--top-center {
  top: 7rem !important;
  width: 400px !important;
  padding: 40px;
}
