.footer {
  background: #12082d;
  padding-top: 70px;
}
.footer a:link,
.footer a:visited {
  color: inherit;
}
.mainDiv {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(239, 240, 242, 0.1);
  padding-bottom: 70px;
}

.footer p {
  font-size: 18px;
  line-height: 26px;
  margin-top: 15px;
  font-weight: bold;
  color: #ffffff;
}
.footer ul {
  list-style: none;
}
.footer ul li {
  line-height: 46px;
}
.footer ul li,
.div2 a {
  font-size: 18px;
  /* color: rgba(255, 255, 255, 0.6) !important; */
  color: var(--grey);
  align-items: center;
   font-weight: normal;
  font-family: var(--gilroy-medium);
}

.div2 {
  padding: 30px 0px;
}
.social_div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.div2 div:last-child a {
  margin-right: 34px;
}
.logoDiv {
  display: flex;
  align-items: center;
}
.scroll {
  align-items: center;
  justify-content: center;
  margin-left: auto;
  font-size: 15px;
}
.newsletter {
  width: 35%;
  padding: 1rem;
  margin-top: -1.5rem;
}
.social_div{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.social_media {
  margin: 20px 0;
}
.social_media img {
  margin: 0px 5px;
}
.scroll {
  /* position: fixed; */
  /* bottom: 2rem; */
  /* right: 2rem; */
  display: none;
  width: 30px;
  height: 30px;
  background: #ffffff;
  border-radius: 10px;
  font-size: 1.5rem;
}

@media screen and (max-width: 1200px) {
  .newsletter {
    width: 100%;
  }
  .mainDiv {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 80px;
    grid-column-gap: 10px;
    justify-content: center;
  }
}
@media screen and (max-width: 646px) {
  .newsletter {
    padding: 0px;
  }
  .learn {
    display: block;
    /* width: 100%;
    font-size: 12px; */
    
  }
  .newsletter {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .div2 {
    font-size: 12px;
  }
  .div2 div:last-child a {
    margin-right: 15px;
  }
  .logoDiv {
    justify-content: space-between;
    width: 100%;
  }
  .scroll{
  display: flex;

  }
  .mainDiv {
    padding-bottom: 30px;
  }
  .div2 {
    padding: 20px 0px;
  }
}
