.container {
  position: absolute;
  width: 100%;
  margin-bottom: 20rem;
  top: 0;
  left: 0;
  z-index: 10000;
  background: #fff;
  height: 100vh;
}
.imgDiv {
  margin: auto;
  width: 80%;
  height: auto;
}
.container p {
  margin-left: auto;
  width: 50px;
  font-size: 4rem;
  cursor: pointer;
}
